.portfolio__footer {
    height: 500px;
    margin-top: 8rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: rgba(4, 13, 18, 0.3);
}

.portfolio__footer-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    padding: 4rem 0;
}

.portfolio__footer-container_quote {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.portfolio__footer-container_quote h1 {
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    margin-bottom: 1rem;
    width: 60%;
    color: #FFFFFF;
}

.portfolio__footer-container_quote p {
    font-family: 'Raleway';
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    width: 40%;
    align-self: flex-end;
    color: #FFFFFF;
}

.portfolio__footer-container_links {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 60%;
}